export default {
  data() {
    return {
			user_id: ''
		};
	},
	onLoad() {
		this.user_id = uni.getStorageSync("user_id");
		// 他的好友接口
		this.getFriend()
	},
  methods: {
		// 他的好友接口
		getFriend() {
			this.$http.get('/wallet/getFriend', { params: { user_id:this.user_id } }).then(res =>{
				console.log(res)
			})
		}
	}
};
